import logo from './logo.svg';
import './App.css';
import { ChakraProvider, Center, Button, useToast } from '@chakra-ui/react'
import { Engine, Matter, Render, Runner, Bodies, Composite } from 'matter-js'

function App() {

  const toast = useToast()

  const engine = Engine.create()

  const render = Render.create({element: document.body, engine: engine})

  const boxA = Bodies.rectangle(400, 200, 80, 80)
  const boxB = Bodies.rectangle(450, 50, 80, 80)
  const ground = Bodies.rectangle(400, 610, 810, 60, {isStatic: true})
  
  Composite.add(engine.world, [boxA, boxB, ground])


  Render.run(render)

  const runner = Runner.create()

  Runner.run(runner, engine)

  return (
    <ChakraProvider>
      <Center>
        <Button onClick={() => toast({description: "Sa karde"})}>
          Bana tkla!
        </Button>


      </Center>
    </ChakraProvider>
  );
}

export default App;
